<script>

    import {setContext} from "svelte"
    import {writable} from "svelte/store"

    const
        user = writable(false);

    const checkAuth = async () => {
        const candidate = await localStorage.getItem('user');

        if(candidate?.access_token) {
            $user = {...candidate};
        }
    }

    checkAuth()

    const isAuth = async () => {
        const candidate = await localStorage.getItem('user');

        if(candidate?.access_token) {
            $user = {...candidate};
            console.log('KEKUS YES')
            return true;
        }

        return false;
    }

    setContext("user", user);
    setContext("isAuth", isAuth)

</script>

<slot />