<script>
	import {setContext} from "svelte"
	import {writable} from "svelte/store"
	import {Core} from "./Core";
	import {Route} from "tinro";
	import View from "@Core/View.svelte"

    import {Login, Home} from "./Pages";

	const FBConnect = writable(false);

    const pages =[
        {
          url: "/",
          component: Home,
          authorization: true
        },
        {
            url: "/login",
            component: Login,
            authorization: false
        }
    ]

    setContext('FBConnect', FBConnect)
</script>


<Core>
    {#await $FBConnect}
        <h1>AWAIT FB CONNECT</h1>
    {:then}
        {#each pages as page}

            <Route path={page.url}>
                <View auth={page.authorization}>
                    <svelte:component this={page.component} />
                </View>
            </Route>

        {/each}
    {/await}
</Core>


<style lang="scss">
	.block {
		& h1 {
			color: blue;
		}
	}
</style>