<script>
    import {getContext} from "svelte";
    import {router} from "tinro"

    const
        _isAuth = getContext("isAuth");

    export let auth = false;

    const checkAuth = async () => {
        const isAuth = await _isAuth();

        if(!$router.query.token && !isAuth) {
            router.goto('/login')
        }
    }
</script>

{#await checkAuth()}
    <div class="block">
        <h1>AWAIT</h1>
    </div>
{:then}
    <div class="page">
        <slot />
    </div>
{/await}